import React, { useState, useEffect } from 'react'
import { getBeer, getImgs } from '../utils/axiosApi'
import Card from '../utils/Card'
import Top from './Top'
import Footer from './Footer'

function Beer() {
  const [beerList, setBeerList] = useState([])
  const [imgs, setImgs] = useState([])
  const [pageNumber, setPageNumber] = useState(1)
  const [total, setTotal] = useState(0)
  const [search, setSearch] = useState('')
  useEffect(() => {
    getDataBeer()
    getDataImgs()
  }, [])

  function getDataBeer() {
    getBeer((data) => {
      setBeerList([...beerList, ...data])
      setTotal(parseInt(data.length / 20))
    })
  }
  function getDataImgs() {
    getImgs((data) => {
      setImgs([...imgs, ...data])
    })
  }

  return (
    <>
      <Top />
      <br />
      <div
        id="main"
        style={{ display: 'flex', width: '100%', justifyContent: 'center' }}
      >
        <div class="input-group mb-3" style={{ width: '70%' }}>
          <input
            type="text"
            class="form-control"
            onChange={(e) => {
              setSearch(e.target.value)
            }}
            placeholder="Search for your Beer"
            aria-label="Username"
            aria-describedby="basic-addon1"
          />
        </div>
      </div>
      <div className="container">
        <div
          className="row row-cols-lg-3 row-cols-md-2 row-cols-sm-2"
          style={{
            display: 'flex',
            width: '100%',
            justifyContent: 'space-between',
          }}
        >
          {beerList
            .slice((pageNumber - 1) * 20, (pageNumber - 1) * 20 + 20)
            .map((ele, index) => {
              return (
                <>
                  {search === '' ? (
                    <Card
                      key={index}
                      img={imgs[index % 5].image}
                      name={ele.name}
                      beerStyle={ele.style}
                      ounces={ele.ounces}
                    />
                  ) : search.toLowerCase() ===
                    ele.name.substring(0, search.length).toLowerCase() ? (
                    <Card
                      key={index}
                      img={imgs[index % 5].image}
                      name={ele.name}
                      beerStyle={ele.style}
                      ounces={ele.ounces}
                    />
                  ) : null}
                </>
              )
            })}
        </div>
      </div>
      <div style={{ display: 'flex', width: '100%', justifyContent: 'center' }}>
        <span
          style={{ cursor: 'pointer', fontSize: 30 }}
          onClick={() => {
            if (pageNumber > 1) {
              setPageNumber(pageNumber - 1)
            }
          }}
        >
          {'< '}
        </span>
        <span style={{ fontSize: 30 }}>
          {pageNumber}
          {'/'}
        </span>
        <span style={{ fontSize: 30 }}>{total}</span>
        <span
          style={{ cursor: 'pointer', fontSize: 30 }}
          onClick={() => {
            if (pageNumber < total) setPageNumber(pageNumber + 1)
          }}
        >
          {'>'}
        </span>
      </div>
      <br />
      <Footer />
    </>
  )
}

export default Beer
