import React from 'react'

function Card({ img, name, beerStyle, ounces }) {
  return (
    <>
      <div className="card mb-3" styles={{ maxWidth: 240 }}>
        <div className="row no-gutters">
          <div className="col-md-4">
            <img src={img} className="card-img" alt="beer" />
          </div>
          <div className="col-md-8">
            <div className="card-body">
              <h5 className="card-title">{name}</h5>
              <p className="card-text">Style: {beerStyle}</p>
              <p className="card-text">
                <small className="text-muted">Ounce:{ounces}</small>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Card
