import React from 'react'
import beer from './static/Beer.jpg'

function Top() {
  return (
    <>
      <div
        style={{
          height: '100vh',
          width: '100%',
          backgroundImage: `url(${beer})`,
          backgroundSize: 'cover',
        }}
      >
        <div
          style={{
            position: 'absolute',
            height: '100%',
            width: '100%',
            backgroundColor: 'black',
            opacity: '0.3',
            top: 0,
          }}
        ></div>
        <div
          style={{
            position: 'absolute',
            height: '30vh',
            width: '40%',

            top: '30%',
            left: '30%',
          }}
        >
          <div
            style={{
              display: 'flex',
              width: '100%',
              justifyContent: 'center',
              flexDirection: 'column',
            }}
          >
            <h1
              style={{
                color: '#fff',
                fontFamily: 'cursive',
              }}
            >
              Enjoy Your Beer
            </h1>
            <h4
              style={{
                color: '#fff',
                fontFamily: 'cursive',
              }}
            >
              “A fine beer may be judged with only one sip, but it's better to
              be thoroughly sure.” – Czech
            </h4>
            <br />
            <a href="#main" className="btn btn-lg btn-light">
              Explore
            </a>
          </div>
        </div>
      </div>
    </>
  )
}

export default Top
